import React from 'react';

import { List, ListItem, makeStyles } from '@material-ui/core';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';

import { BaseType } from '../../../lib/api';

const useStyles = makeStyles((theme) => ({
  root: {},
  listItem: {
    cursor: 'pointer',
    display: 'grid',
    gridTemplateColumns: '3em auto',
  },
  idWrap: {
    display: 'flex',
    alignItems: 'center',

    color: theme.palette.primary.main,
    '& > span': {
      minWidth: '2em',
      opacity: 0.5,
      textAlign: 'center',
    },
    '& svg': {
      position: 'relative',
      left: '-4px',
    },
  },
  labelWrap: {
    fontSize: '1.2em',
    paddingLeft: theme.spacing(1),
    color: 'cadetblue',
  },
}));

type ListViewProps = {
  className?: string;
  items: BaseType[];
  showDivider?: boolean;
  onItemSelected: (item: BaseType) => void;
};

const ListView: React.FC<ListViewProps> = (props: ListViewProps) => {
  const classes = useStyles();
  return (
    <List className={props.className}>
      {props.items.map((item) => (
        <ListItem
          classes={{ root: classes.listItem }}
          divider={props.showDivider}
          key={item.id}
          onClick={(): void => props.onItemSelected(item)}>
          <div className={classes.idWrap}>
            <span>{item.id}</span>
            <ArrowForwardIosIcon />
          </div>
          <div className={classes.labelWrap}>{item.value}</div>
        </ListItem>
      ))}
    </List>
  );
};

export default ListView;
