import { useHistory } from 'react-router-dom';

type useNavigationType = () => [(route: string) => () => void];

export const useNavigation: useNavigationType = () => {
  const history = useHistory();
  const navigateTo = (route: string) => {
    return (): void => {
      history.push(route);
    };
  };
  return [navigateTo];
};
