import { useDispatch, useSelector } from 'react-redux';
import { useFirestore } from 'react-redux-firebase';
import { getKural, SearchResult, SearchTypes } from 'src/lib/api';
import { AppState } from 'src/state';
import {
  appendSearchResults,
  clearSearchResults,
  setSearchFilters,
  setSearchKeyword,
  setSearchStatus,
} from 'src/state/search';

import { isCategoryValid } from '../api';

type UseKuralSearchType = () => [
  (keyword: string) => void,
  (status: boolean) => void
];

const findSuitableSearchTypes = (keyword: string): SearchTypes[] => {
  const types: SearchTypes[] = [];
  try {
    if (Number.isInteger(parseInt(keyword))) {
      const searchNumber = parseInt(keyword);
      isCategoryValid('kural', searchNumber) && types.push('ByKuralNo');
      isCategoryValid('chapter', searchNumber) && types.push('ByChapterNo');
      isCategoryValid('literature', searchNumber) &&
        types.push('ByLiteratureNo');
      isCategoryValid('section', searchNumber) && types.push('BySectionNo');
    }
  } catch (e) {}
  return types;
};

export const useKuralSearch: UseKuralSearchType = () => {
  const dispatch = useDispatch();
  const firestore = useFirestore();
  const USER_LANG = useSelector(
    (st: AppState) => st.preferences.preference.language
  );
  const { literature, section, chapter } = useSelector(
    (st: AppState) => st.kuralIndexMap
  );
  const setSearchKey = (keyword: string): void => {
    dispatch(setSearchKeyword(keyword));
    const searchTypes = findSuitableSearchTypes(keyword);
    dispatch(setSearchFilters(searchTypes));
    dispatch(clearSearchResults());
    addResults(searchTypes, keyword);
  };

  const addResults = (searchTypes: SearchTypes[], keyword: string): void => {
    searchTypes.forEach(async (type) => {
      const items: SearchResult[] = [];
      switch (type) {
        case 'ByKuralNo':
          const kuralNo = parseInt(keyword);
          if (isCategoryValid('kural', kuralNo)) {
            const item = await getKural(
              firestore,
              parseInt(keyword),
              USER_LANG.primary
            );
            items.push({
              type,
              keyword,
              category: 'kural',
              item,
            });
          }
          break;
        case 'ByChapterNo':
          const chapterNo = parseInt(keyword);
          if (isCategoryValid('chapter', chapterNo)) {
            items.push({
              type,
              keyword,
              item: chapter[parseInt(keyword)],
              category: 'chapter',
            });
          }
          break;
        case 'ByLiteratureNo':
          const literatureNo = parseInt(keyword);
          if (isCategoryValid('literature', literatureNo)) {
            items.push({
              type,
              keyword,
              item: literature[parseInt(keyword)],
              category: 'literature',
            });
          }
          break;
        case 'BySectionNo':
          const sectionNo = parseInt(keyword);
          if (isCategoryValid('section', sectionNo)) {
            items.push({
              type,
              keyword,
              item: section[parseInt(keyword)],
              category: 'section',
            });
          }
          break;
      }
      if (items.length) {
        dispatch(appendSearchResults(items));
      }
    });
  };

  const toggleSearch = (status: boolean): void => {
    dispatch(setSearchStatus(status));
  };
  return [setSearchKey, toggleSearch];
};
