import * as React from 'react';

import { Avatar, IconButton, makeStyles } from '@material-ui/core';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import { KeyboardBackspace, Person, Settings } from '@material-ui/icons';
import { debug } from 'loglevel';
import { matchPath, useHistory } from 'react-router-dom';

import KuralSearch from '../kural/KuralSearch';
import HideOnScroll from './HideOnScroll';

const useStyles = makeStyles((theme) => ({
  toolbar: {
    padding: 0,
    justifyContent: 'space-around',
  },
  avatar: {
    color: 'white',
    marginLeft: theme.spacing(1),
    backgroundColor: theme.palette.primary.main,
  },
  actionsGroup: {},
  searchbar: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
    flexGrow: 1,
  },
  iconButton: {
    padding: '6px',
    '& svg': {
      fontSize: '2rem',
    },
  },
}));

type MyAppBarProps = {
  actions?: React.ReactNode;
};

const MyAppBar: React.FC<MyAppBarProps> = (props: MyAppBarProps) => {
  debug('MyAppBar');
  const classes = useStyles();
  const history = useHistory();
  const isHomeRoute = (): boolean =>
    matchPath(history.location.pathname, { path: '/' })?.isExact || false;
  const renderLeftIcon = (): React.ReactElement => {
    return !isHomeRoute() ? (
      <IconButton className={classes.iconButton} onClick={history.goBack}>
        <KeyboardBackspace color='primary' />
      </IconButton>
    ) : (
      <Avatar className={classes.avatar}>
        <Person />
      </Avatar>
    );
  };

  return (
    <>
      <HideOnScroll direction='down' {...props}>
        <AppBar color='transparent' elevation={0}>
          <Toolbar className={classes.toolbar}>
            {renderLeftIcon()}
            <KuralSearch className={classes.searchbar} />
            <div className={classes.actionsGroup}>
              {props.actions}
              <IconButton
                className={classes.iconButton}
                onClick={(): void => history.push('/settings')}>
                <Settings color='primary' />
              </IconButton>
            </div>
          </Toolbar>
        </AppBar>
      </HideOnScroll>
      <Toolbar />
    </>
  );
};

export default MyAppBar;
