import React from 'react';

import { useSubCategory } from '../../../lib/hooks';
import { ListKuralCard } from './ListKuralCard';

type ListKuralsOfChapterProps = {
  className?: string;
  chapterNo: number;
};

export const ListKuralsOfChapter: React.FC<ListKuralsOfChapterProps> = (
  props: ListKuralsOfChapterProps
) => {
  const [kurals] = useSubCategory('chapter', props.chapterNo, 'kural');
  return <ListKuralCard kurals={kurals} />;
};
