import * as React from 'react';

import {
  makeStyles,
  BottomNavigation,
  BottomNavigationAction,
} from '@material-ui/core';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import clsx from 'clsx';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  appBar: {
    bottom: 0,
    top: 'auto',
    boxShadow: 'none',
    borderTop: '1px solid rgba(0, 0, 0, 0.12)',
  },
}));

export type MyBottomNavigationBarItemsType = {
  icon: React.ReactNode;
  label: string;
  route: string;
};
export type MyBottomNavigationBarProps = {
  items: Array<MyBottomNavigationBarItemsType>;
  currentRoute: string;
  className?: string;
  onChange: (route: string) => void;
};

const MyBottomNavigationBar: React.FC<MyBottomNavigationBarProps> = (
  props: MyBottomNavigationBarProps
) => {
  const classes = useStyles();
  return (
    <div className={clsx(classes.root, props.className)}>
      <AppBar position='fixed' color='inherit' className={classes.appBar}>
        <BottomNavigation
          value={props.currentRoute}
          onChange={(_, value: string): void => props.onChange(value)}
          className={classes.root}>
          {props.items.map((item, index) => (
            <BottomNavigationAction
              label={item.label}
              value={item.route}
              icon={item.icon}
              key={index}
            />
          ))}
        </BottomNavigation>
      </AppBar>
      <Toolbar />
    </div>
  );
};

export default MyBottomNavigationBar;
