import { ExtendedFirestoreInstance } from 'react-redux-firebase';
import { ThunkAction } from 'redux-thunk';
import { updateUserPreference, User, UserPreference } from 'src/lib/api';

import { AppState } from '../reducers';

export type SetUserAction = { type: 'SetUser'; payload: User };
export type UpdateSettingsAction = {
  type: 'UpdateSettings';
  payload: Partial<UserPreference>;
};
type SettingsActionTypes = SetUserAction | UpdateSettingsAction;
export const SETTINGS_STORAGE = 'settings';

export const updateUserFSAsync = (
  firestore: ExtendedFirestoreInstance,
  user: User
): ThunkAction<void, AppState, unknown, SetUserAction> => {
  return async (dispatch) => {
    dispatch(setUser(user));
    await updateUserPreference(firestore, user);
  };
};

export const setUser = (user: User): SetUserAction => ({
  type: 'SetUser',
  payload: user,
});

export const applyPreference = (
  pref: Partial<UserPreference>
): UpdateSettingsAction => {
  return {
    type: 'UpdateSettings',
    payload: pref,
  };
};

export const DEFAULT: User = {
  id: 'anonymous',
  favorites: {
    kural: [],
    chapter: [],
  },
  preference: {
    language: {
      primary: 'TA',
      secondary: [],
    },
    fontSize: 16,
  },
};
export const PreferenceReducer = (
  settings = DEFAULT,
  action: SettingsActionTypes
): User => {
  switch (action.type) {
    case 'SetUser':
      return action.payload;
    case 'UpdateSettings':
      return {
        ...settings,
        preference: { ...settings.preference, ...action.payload },
      };
    default:
      return settings;
  }
};
