import { BaseType, Category } from 'src/lib/api';

type isItemValidType = (key: number) => boolean;
const isSectionNoValid: isItemValidType = (section) =>
  section >= 1 && section <= 3;
const isLiteratureNoValid: isItemValidType = (literature) =>
  literature >= 1 && literature <= 13;
const isChapterNoValid: isItemValidType = (chapter) =>
  chapter >= 1 && chapter <= 133;
const isKuralNoValid: isItemValidType = (kuralNo) =>
  kuralNo >= 1 && kuralNo <= 1330;

type getItemByPositionType = (key: number) => number;
const getSectionPrev: getItemByPositionType = (section) =>
  section === 1 ? 3 : section - 1;
const getSectionNext: getItemByPositionType = (section) =>
  section === 3 ? 1 : section + 1;
const getLiteraturePrev: getItemByPositionType = (literature) =>
  literature === 1 ? 13 : literature - 1;
const getLiteratureNext: getItemByPositionType = (literature) =>
  literature === 13 ? 1 : literature + 1;
const getChapterPrev: getItemByPositionType = (chapter) =>
  chapter === 1 ? 133 : chapter - 1;
const getChapterNext: getItemByPositionType = (chapter) =>
  chapter === 133 ? 1 : chapter + 1;
const getKuralPrev: getItemByPositionType = (kural) =>
  kural === 1 ? 1330 : kural - 1;
const getKuralNext: getItemByPositionType = (kural) =>
  kural === 1330 ? 1 : kural + 1;

export const isItemInRange = (item: number, range: [number, number]): boolean =>
  item >= range[0] && item <= range[1];

export const fillRange = (range: [number, number]): number[] => {
  const out = [];
  for (let i = range[0]; i <= range[1]; i++) {
    out.push(i);
  }
  return out;
};

export const isCategoryValid = (type: Category, id: number): boolean => {
  switch (type) {
    case 'section':
      return isSectionNoValid(id);
    case 'literature':
      return isLiteratureNoValid(id);
    case 'chapter':
      return isChapterNoValid(id);
    case 'kural':
      return isKuralNoValid(id);
  }
};

export const isCategoryValidErr = (type: Category, id: number): void => {
  if (!isCategoryValid(type, id)) {
    throw new Error(`${type} with ${id} not found`);
  }
};

export const getItemByPosition = (
  type: Category,
  direction: 'PREV' | 'NEXT',
  id: number
): number => {
  switch (type) {
    case 'section':
      return direction === 'NEXT' ? getSectionNext(id) : getSectionPrev(id);
    case 'literature':
      return direction === 'NEXT'
        ? getLiteratureNext(id)
        : getLiteraturePrev(id);
    case 'chapter':
      return direction === 'NEXT' ? getChapterNext(id) : getChapterPrev(id);
    case 'kural':
      return direction === 'NEXT' ? getKuralNext(id) : getKuralPrev(id);
  }
};

type BaseTypeMap = Record<number, BaseType>;
export const toBaseTypeMap = (list: BaseType[]): BaseTypeMap => {
  return list.reduce(
    (oldVal, item) => ({ ...oldVal, [item.id]: item }),
    {} as BaseTypeMap
  );
};
