import React from 'react';

import { debug } from 'loglevel';
// import { useHistory, useParams } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { useHistory, useParams } from 'react-router-dom';
import { findCategoryRouteMapString, ITEMS_COUNT_MAP } from 'src/lib/api';
import { ROUTE_CONSTANTS } from 'src/routes';
import { AppState } from 'src/state';
import SwiperCore, { Virtual, Keyboard } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';

import { ListKuralsOfChapter } from './ListKuralsOfChapter';

import 'swiper/swiper.scss';

SwiperCore.use([Virtual, Keyboard]);

type KuralListSwipeableProps = {
  className?: string;
};

export const KuralListSwipeable: React.FC<KuralListSwipeableProps> = (
  props: KuralListSwipeableProps
) => {
  const INDEX_MAP = useSelector((st: AppState) => st.kuralIndexMap.index);
  const history = useHistory();
  const { chapter } = useParams<{ chapter: string }>();
  const CHAPTER_NO = parseInt(chapter);
  const [activeChapterNo, setActiveChapterNo] = React.useState(CHAPTER_NO);
  const slideChapterIds = Array.from(Array(ITEMS_COUNT_MAP.chapter).keys()).map(
    (i) => i + 1
  );
  React.useEffect(() => {
    if (INDEX_MAP) {
      const newRoute = `${
        ROUTE_CONSTANTS.LIST
      }/${findCategoryRouteMapString(INDEX_MAP, { chapter: activeChapterNo })}`;
      debug('replace route to => ', newRoute);
      history.replace(newRoute);
    }
  }, [INDEX_MAP, activeChapterNo, history]);
  const renderSlide = (chId: number, index: number) => (
    <SwiperSlide key={chId} virtualIndex={index}>
      <ListKuralsOfChapter chapterNo={chId} />
    </SwiperSlide>
  );
  return (
    <div className={props.className}>
      <Swiper
        keyboard={true}
        grabCursor={true}
        initialSlide={CHAPTER_NO!! - 1}
        spaceBetween={10}
        slidesPerView={1}
        allowSlideNext={
          slideChapterIds[slideChapterIds.length - 1] !== activeChapterNo
        }
        virtual
        onTransitionEnd={(swiper) => {
          setActiveChapterNo(swiper.activeIndex + 1);
        }}>
        {slideChapterIds.map((chId, index) => renderSlide(chId, index))}
      </Swiper>
    </div>
  );
};
