import { getCategoryIds } from './category';
import { findChapterNoFromKuralNo } from './common';
import { Category, IndexMapType, RootCategory } from './types';

type TopRouteMap = Partial<Record<RootCategory, number>>;
export type CategoryIdMapPartial = Partial<Record<Category, number>>;

const rootCategoryFinder = (
  INDEX_MAP: IndexMapType,
  options: TopRouteMap
): TopRouteMap => {
  const category = Object.keys(options).find(
    (k: string) => options[k as RootCategory]
  ) as RootCategory;
  return INDEX_MAP && category
    ? // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
      getCategoryIds(INDEX_MAP, category, options[category]!!)
    : {};
};

export const findCategoryRouteMap = (
  INDEX_MAP: IndexMapType,
  filter: CategoryIdMapPartial
): CategoryIdMapPartial => {
  const { kural, chapter, literature, section } = filter;
  const KURALS_CHAPTER = kural ? findChapterNoFromKuralNo(kural) : chapter;
  return {
    ...rootCategoryFinder(INDEX_MAP, {
      chapter: KURALS_CHAPTER,
      literature,
      section,
    }),
    ...(kural ? { kural } : {}),
  };
};

export const convertCategoryRouteMapToString = (
  filter: CategoryIdMapPartial
): string => {
  return (['section', 'literature', 'chapter', 'kural'] as Category[])
    .filter((k) => filter[k as Category] !== undefined)
    .map((k) => filter[k as Category])
    .join('/');
};

export const findCategoryRouteMapString = (
  INDEX_MAP: IndexMapType,
  filter: CategoryIdMapPartial
): string => {
  return convertCategoryRouteMapToString(
    findCategoryRouteMap(INDEX_MAP, filter)
  );
};
