import { ExtendedFirestoreInstance } from 'react-redux-firebase';
import { ThunkAction } from 'redux-thunk';
import {
  getAllChapter,
  getAllLiterature,
  getAllSections,
  getAllTransliterators,
  getKuralIndex,
  getUserPreferences,
  toBaseTypeMap,
  Transliterator,
} from 'src/lib/api';

import { AppState } from '../reducers';
import { setUser, SetUserAction } from './../settings';
import { InitIndexMapAction } from './types';

export const thunkInitKuralIndexMapAndUserPref = (
  firestore: ExtendedFirestoreInstance
): ThunkAction<void, AppState, unknown, InitIndexMapAction | SetUserAction> => {
  return async (dispatch): Promise<void> => {
    // TODO: fetch data only in userPrimary Language
    // TODO: add userPreferences separate snapshot listener
    const user = await getUserPreferences(firestore, 'rajeshsundaram');
    const { primary } = user.preference.language;
    const kuralIndex = await getKuralIndex(firestore);

    const sectionMap = toBaseTypeMap(await getAllSections(firestore, primary));
    const literatureMap = toBaseTypeMap(
      await getAllLiterature(firestore, primary)
    );
    const chapterMap = toBaseTypeMap(await getAllChapter(firestore, primary));
    const transliteratorMap = (await getAllTransliterators(firestore)).reduce(
      (oldVal, t1) => ({ ...oldVal, [t1.id]: t1 }),
      {}
    ) as Record<number, Transliterator>;
    dispatch(setUser(user));
    dispatch({
      type: 'init',
      payload: {
        section: sectionMap,
        chapter: chapterMap,
        literature: literatureMap,
        index: kuralIndex,
        transliterator: transliteratorMap,
      },
    });
  };
};
