import React from 'react';

import { makeStyles } from '@material-ui/core';
import List from '@material-ui/core/List';
import { useSelector } from 'react-redux';
import { findCategoryRouteMapString, Kural } from 'src/lib/api';
import { useNavigation } from 'src/lib/hooks';
import { ROUTE_CONSTANTS } from 'src/routes';
import { AppState } from 'src/state';

import KuralCard from './KuralCard';

const useStyles = makeStyles((theme) => ({
  list: {
    padding: theme.spacing(1),
    paddingTop: '0px',
    '& li:not(:last-child)': {
      marginBottom: theme.spacing(1),
    },
  },
  kuralCard: {
    padding: 0,
  },
}));

type ListKuralCardProps = {
  kurals: Kural[];
};

export const ListKuralCard: React.FC<ListKuralCardProps> = (
  props: ListKuralCardProps
) => {
  const classes = useStyles();
  const INDEX_MAP = useSelector((st: AppState) => st.kuralIndexMap.index);
  const [navigateTo] = useNavigation();
  const openKuralDetail = (kural: Kural): void =>
    navigateTo(
      `${ROUTE_CONSTANTS.LIST}/${findCategoryRouteMapString(INDEX_MAP!!, {
        kural: kural.id,
      })}`
    )();
  return (
    <List className={classes.list}>
      {props.kurals.map((k, i) => (
        <KuralCard
          showBadge={true}
          className={classes.kuralCard}
          key={k.id}
          kural={k}
          onClick={openKuralDetail}
        />
      ))}
    </List>
  );
};
