import firebase from 'firebase/app';
import log from 'loglevel';
import { ReactReduxFirebaseProviderProps } from 'react-redux-firebase';
import { createFirestoreInstance } from 'redux-firestore';
import 'firebase/firestore';
import 'firebase/auth';

import { AppStore } from './state';

const {
  REACT_APP_API_KEY,
  REACT_APP_AUTH_DOMAIN,
  REACT_APP_DB_URL,
  REACT_APP_PROJECT_ID,
  REACT_APP_STORAGE_BUCKET,
  REACT_APP_MESSAGING_SENDER_ID,
  REACT_APP_FB_APP_ID,
  REACT_APP_MESUREMENT_ID,
} = process.env;

((): void => {
  if (
    !(
      REACT_APP_API_KEY &&
      REACT_APP_AUTH_DOMAIN &&
      REACT_APP_DB_URL &&
      REACT_APP_PROJECT_ID
    )
  ) {
    throw new Error('FB_CONF missing');
  }
})();
const FIREBASE_CONFIG = {
  apiKey: REACT_APP_API_KEY,
  projectId: REACT_APP_PROJECT_ID,
  databaseURL: REACT_APP_DB_URL,
  authDomain: REACT_APP_AUTH_DOMAIN,
  storageBucket: REACT_APP_STORAGE_BUCKET,
  messagingSenderId: REACT_APP_MESSAGING_SENDER_ID,
  appId: REACT_APP_FB_APP_ID,
  measurementId: REACT_APP_MESUREMENT_ID,
};
firebase.initializeApp(FIREBASE_CONFIG);
firebase
  .firestore()
  .enablePersistence({ synchronizeTabs: true })
  .catch((err) => {
    if (err.code === 'unimplemented') {
      log.error('persistence not supported on the current browser');
    }
  });

export const REDUX_FIREBASE_CONF: ReactReduxFirebaseProviderProps = {
  firebase,
  dispatch: AppStore.dispatch,
  createFirestoreInstance,
  config: { userProfile: 'users', useFirestoreForProfile: true },
};
