import React from 'react';

import { makeStyles } from '@material-ui/core';
import ListItem from '@material-ui/core/ListItem';
import clsx from 'clsx';
import { Kural } from 'src/lib/api';

const useStyles = makeStyles((theme) => ({
  listItem: {
    display: 'flex',
    flexDirection: 'column',
    userSelect: 'none',
    alignItems: 'start',
    [theme.breakpoints.up('sm')]: {
      cursor: 'pointer',
    },
  },
  badgeContainer: {
    background: theme.palette.primary.main,
    color: 'white',
    height: '1em',
    fontSize: '0.6em',
    lineHeight: '0.8em',
    borderRadius: '1em',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    padding: '2px 6px',
    minWidth: '1em',
  },
  kuralWrapper: {
    width: '100%',
  },
}));

type KuralCardProps = {
  kural: Kural;
  onClick?: (kural: Kural) => void;
  className?: string;
  showBadge?: boolean;
  /**
   * The component used for the root node.
   * e.g) a, li
   */
  componentType?: 'div' | 'span';
};
const KuralCard: React.FC<KuralCardProps> = (props: KuralCardProps) => {
  const classes = useStyles();
  return (
    <ListItem
      className={props.className}
      classes={{ root: clsx(classes.listItem) }}
      component={props.componentType || 'li'}>
      {props.showBadge && (
        <div className={classes.badgeContainer}>{props.kural.id}</div>
      )}
      <div
        onClick={(): void => props.onClick && props.onClick(props.kural)}
        className={classes.kuralWrapper}
        dangerouslySetInnerHTML={{
          __html: props.kural.value.replace('\n', '<br>'),
        }}></div>
    </ListItem>
  );
};

export default KuralCard;
