import React from 'react';

import {
  makeStyles,
  List,
  ListItem,
  Collapse,
  ListItemText,
  ListSubheader,
  Badge,
} from '@material-ui/core';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import clsx from 'clsx';
import { useSelector } from 'react-redux';
import { useFirestore } from 'react-redux-firebase';
import { findChapterNoFromKuralNo, getKuralByIds } from 'src/lib/api';
import { Kural, BaseType } from 'src/lib/api';
import { useBaseItem } from 'src/lib/hooks';
import { useNavigation } from 'src/lib/hooks';
import { AppState } from 'src/state';

import ListView from '../common/ListView';
import KuralCard from './KuralCard';

const useStyles = makeStyles((theme) => ({
  root: {
    // marginTop: theme.spacing(2),
    // padding: theme.spacing(1),
  },
  collapsableGroup: {
    // marginBottom: theme.spacing(1),
  },
  list: {
    '& .sub-header': {
      textAlign: 'left',
      color: 'brown',
      lineHeight: '2rem',
      padding: `0px 0 0 ${theme.spacing(0.5)}px`,
    },
  },
  listItemTitle: {
    '& .badge-placeholder': {
      height: '0.5rem',
    },
    '& .expand-toggle': {
      marginLeft: theme.spacing(1.5),
    },
  },
  listTitleBadge: {
    opacity: 0.6,
    fontSize: '0.6rem',
    height: '130%',
    width: '120%',
  },
  kural: {
    padding: `0 0 0 ${theme.spacing(1)}px`,
    '&:not(:last-child)': {
      marginBottom: theme.spacing(1),
    },
  },
  chapFavorites: {
    padding: 0,
    '& li': {
      paddingLeft: theme.spacing(0.5),
      paddingRight: theme.spacing(0.5),
    },
  },
}));

type FavoritesViewerProps = {
  className?: string;
  expand?: boolean;
};

const FavoritesViewer: React.FC<FavoritesViewerProps> = (
  props: FavoritesViewerProps
) => {
  const [getChapter] = useBaseItem('chapter');
  const firestore = useFirestore();
  const {
    favorites: { kural: KURAL_IDS, chapter: CHAPTER_IDS },
    preference,
  } = useSelector((st: AppState) => st.preferences);
  const [kurals, setKurals] = React.useState<Kural[]>([]);
  const [isKuralVisible, setKuralVisible] = React.useState<boolean>(
    props.expand ?? true
  );
  const [isChapterVisible, setChapterVisible] = React.useState<boolean>(
    props.expand ?? false
  );
  const [navigateTo] = useNavigation();
  const classes = useStyles();
  // TODO:
  const onChapterSelected = (item: BaseType): void => {
    navigateTo(`/list/${item.id}/kural`)();
  };
  const onKuralSelected = (item: BaseType): void => {
    navigateTo(`/list/kural/${item.id}`)();
  };
  React.useEffect(() => {
    if (KURAL_IDS.length > 0) {
      getKuralByIds(firestore, KURAL_IDS, preference.language.primary).then(
        (kurals) => {
          setKurals(kurals);
        }
      );
    }
  }, [firestore, KURAL_IDS, preference.language.primary]);
  const chapterKuralMap = new Map<number, BaseType[]>([]);
  kurals.forEach((it) => {
    const chap = findChapterNoFromKuralNo(it.id);
    !chapterKuralMap.has(chap)
      ? chapterKuralMap.set(chap, [it])
      : chapterKuralMap.set(chap, [...(chapterKuralMap.get(chap) || []), it]);
  });
  const noRecordsFound = <h1>None Found</h1>;

  const GroupTitleMaker = (
    isVisible: boolean,
    onClick: () => void,
    title: React.ReactNode,
    badgeContent: React.ReactNode
  ) => (
    <ListItem
      selected={isVisible}
      className={classes.listItemTitle}
      button
      onClick={onClick}>
      <ListItemText inset={true} primary={title} />
      <Badge
        badgeContent={badgeContent}
        classes={{ badge: classes.listTitleBadge }}
        color='secondary'
        showZero={false}
        max={10}
        invisible={isVisible}>
        <div className='badge-placeholder'></div>
      </Badge>
      <div className='expand-toggle'>
        {isVisible ? <ExpandLess /> : <ExpandMore />}
      </div>
    </ListItem>
  );
  return (
    <div className={clsx(props.className, classes.root)}>
      <List className={classes.list} disablePadding>
        {GroupTitleMaker(
          isKuralVisible,
          () => {
            setKuralVisible(!isKuralVisible);
          },
          'Kurals',
          KURAL_IDS.length
        )}
        <Collapse
          in={isKuralVisible}
          style={{ paddingBottom: isKuralVisible ? 8 : 0 }}
          className={classes.collapsableGroup}
          timeout='auto'>
          {chapterKuralMap.size > 0 &&
            Array.from(chapterKuralMap.keys()).map((k) => {
              const chapterInfo = getChapter(k);
              return (
                <List
                  component='div'
                  className='sub-list'
                  key={k}
                  disablePadding>
                  {chapterInfo && (
                    <ListSubheader
                      className='sub-header'
                      disableSticky
                      onClick={(): void => onChapterSelected(chapterInfo)}>
                      {chapterInfo.value}
                    </ListSubheader>
                  )}

                  {chapterKuralMap.get(k)?.map((item) => (
                    <KuralCard
                      className={classes.kural}
                      key={item.id}
                      kural={item}
                      onClick={onKuralSelected}
                    />
                  ))}
                </List>
              );
            })}
          {chapterKuralMap.size === 0 && noRecordsFound}
        </Collapse>
        {GroupTitleMaker(
          isChapterVisible,
          () => {
            setChapterVisible(!isChapterVisible);
          },
          'Chapters',
          CHAPTER_IDS.length
        )}
        <Collapse
          in={isChapterVisible}
          style={{ paddingBottom: isChapterVisible ? 8 : 0 }}
          className={classes.collapsableGroup}
          timeout='auto'>
          {CHAPTER_IDS.length > 0 && (
            <ListView
              className={classes.chapFavorites}
              items={
                CHAPTER_IDS.sort()
                  .map((ch) => getChapter(ch))
                  .filter((ch) => ch !== null) as BaseType[]
              }
              onItemSelected={onChapterSelected}
            />
          )}
          {CHAPTER_IDS.length === 0 && noRecordsFound}
        </Collapse>
      </List>
    </div>
  );
};

export default FavoritesViewer;
