import React from 'react';

import { Provider } from 'react-redux';

import { AppState } from './reducers';
import { AppStore } from './store';
import { mockStore } from './test';

type StoreProvideType = {
  children: React.ReactNode;
};

const StoreProvider: React.FC<StoreProvideType> = ({
  children,
}: StoreProvideType) => {
  return <Provider store={AppStore}>{children}</Provider>;
};

type MockStoreProps = {
  children: React.ReactNode;
  store: AppState;
};

const WrapMockStore: React.FC<MockStoreProps> = ({
  children,
  store,
}: MockStoreProps) => {
  return <Provider store={mockStore(store)}>{children}</Provider>;
};

export { StoreProvider, AppStore, WrapMockStore };
export type { AppState } from './reducers';
