import firebase from 'firebase';
import log from 'loglevel';

export const firestoreGetCacheFirst = async (
  ref:
    | firebase.firestore.CollectionReference<firebase.firestore.DocumentData>
    | firebase.firestore.Query<firebase.firestore.DocumentData>
): Promise<
  firebase.firestore.QuerySnapshot<firebase.firestore.DocumentData>
> => {
  let result: firebase.firestore.QuerySnapshot<firebase.firestore.DocumentData>;
  try {
    result = await ref.get({ source: 'cache' });
    if (result.empty) {
      throw new Error('result not exists in cache');
    }
    log.debug(`fetches from cache`);
  } catch (err) {
    result = await ref.get({ source: 'server' });
  }
  return result;
};
