type SetTitleAction = { type: 'SetTitle'; payload: string };
type SetDefaultTitleAction = { type: 'DefaultTitle' };
type SetAppendTitleAction = { type: 'AppendTitle'; payload: string };
type TitleActionTypes =
  | SetTitleAction
  | SetDefaultTitleAction
  | SetAppendTitleAction;

export const setTitle = (title: string): SetTitleAction => {
  return {
    type: 'SetTitle',
    payload: title,
  };
};
export const appendTitle = (suffix: string): SetAppendTitleAction => {
  return {
    type: 'AppendTitle',
    payload: suffix,
  };
};

export const setDefaultTitle = (): SetDefaultTitleAction => {
  return { type: 'DefaultTitle' };
};
const defalutTitle = 'Thirukural';
export const TitleReducer = (
  title = defalutTitle,
  action: TitleActionTypes
): string => {
  switch (action.type) {
    case 'SetTitle':
      return action.payload;
    case 'AppendTitle':
      return defalutTitle.concat(`-${action.payload}`);
    case 'DefaultTitle':
      return defalutTitle;
    default:
      return title;
  }
};
