import * as React from 'react';

import { makeStyles } from '@material-ui/core';
import { useSelector, useDispatch } from 'react-redux';
import { useFirestore } from 'react-redux-firebase';
import { renderRoutes } from 'react-router-config';
import { HashRouter as Router } from 'react-router-dom';

import { routes } from './routes';
import { thunkInitKuralIndexMapAndUserPref } from './state/kuralIndex';
import { AppState } from './state/reducers';

const useStyles = makeStyles((theme) => ({
  root: {
    [theme.breakpoints.up('sm')]: {
      fontSize: '3vw',
    },
  },
}));

const App: React.FC = () => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const firestore = useFirestore();
  const settings = useSelector((st: AppState) => st.preferences);
  React.useEffect(() => {
    dispatch(thunkInitKuralIndexMapAndUserPref(firestore));
  }, [dispatch, firestore]);
  return (
    <div
      className={classes.root}
      style={{ fontSize: settings.preference.fontSize }}>
      <Router>{renderRoutes(routes)}</Router>
    </div>
  );
};

export default App;
