import React from 'react';

import { makeStyles } from '@material-ui/core';
import HomeIcon from '@material-ui/icons/Home';
import ListIcon from '@material-ui/icons/List';
import { useDispatch } from 'react-redux';
import { useLocation, useRouteMatch } from 'react-router-dom';
import { useNavigation } from 'src/lib/hooks';
import { setDefaultTitle, setTitle } from 'src/state/title';

import MyBottomNavigationBar from './../common/MyBottomNavigationBar';

const useStyles = makeStyles((theme) => ({
  root: {},
}));

type KuralActionBarProps = Record<string, unknown>;
const ActionBarItems = [
  {
    icon: <HomeIcon />,
    route: '/',
    label: 'Home',
  },
  {
    icon: <ListIcon />,
    route: '/list',
    label: 'List',
  },
  // {
  //   icon: <SearchIcon />,
  //   route: '/search',
  //   label: 'Search',
  // },
  // {
  //   icon: <FavoriteIcon />,
  //   route: '/favorite',
  //   label: 'Favorites',
  // },
  // {
  //   icon: <SettingsIcon />,
  //   route: '/settings',
  //   label: 'Settings',
  // },
];

const KuralActionBar: React.FC<KuralActionBarProps> = (
  props: KuralActionBarProps
) => {
  const homePath = useRouteMatch('/');
  const listPath = useRouteMatch('/list*');
  const location = useLocation();
  const dispatch = useDispatch();

  const initRoute = React.useCallback(
    (): string =>
      homePath?.isExact
        ? ActionBarItems[0].route
        : listPath?.isExact
        ? ActionBarItems[1].route
        : location.pathname,
    [homePath, listPath, location.pathname]
  );
  const [route, setRoute] = React.useState(initRoute());
  React.useEffect(() => {
    setRoute(initRoute());
  }, [initRoute, location]);
  const classes = useStyles();
  const [navigateTo] = useNavigation();
  const navigateBar = (route: string): void => {
    setRoute(route);
    navigateTo(route)();
    if (ActionBarItems[0].route === route) {
      dispatch(setDefaultTitle());
    } else {
      const label = ActionBarItems.find((it) => it.route === route)?.label;
      if (label) {
        dispatch(setTitle(label));
      }
    }
  };
  return (
    <MyBottomNavigationBar
      items={ActionBarItems}
      currentRoute={route}
      onChange={navigateBar}
      className={classes.root}
    />
  );
};

export default KuralActionBar;
