import React from 'react';

import { makeStyles, ListItem, List, Avatar } from '@material-ui/core';
import clsx from 'clsx';
import { useSelector } from 'react-redux';
import { isResultOfType, SearchResult, CATEGORY_COLORS } from 'src/lib/api';
import { useNavigation } from 'src/lib/hooks';
import { AppState } from 'src/state/reducers';
import KuralCard from 'src/views/components/kural/KuralCard';

import { SearchChips } from './SearchChips';

const useStyles = makeStyles((theme) => ({
  root: {
    marginTop: theme.spacing(2),
    padding: theme.spacing(1),
  },
  kuralSearchItem: {
    margin: 0,
    padding: 0,
    borderBottom: 'none',
  },
  searchItem: {
    paddingLeft: theme.spacing(1),
    paddingRight: theme.spacing(1),
    cursor: 'pointer',
  },
  searchItemAvatar: {
    width: '2rem',
    height: '2rem',
    textAlign: 'center',
    fontSize: '1rem',
    marginRight: '0.5rem',
  },
}));

type SearchProps = {
  className?: string;
};

type SearchResultItemProps = {
  component?: React.ReactNode;
  result: SearchResult;
  label: string;
  onClick?: () => void;
};
const SearchResultItem: React.FC<SearchResultItemProps> = ({
  result,
  label,
  onClick,
  component,
}: SearchResultItemProps) => {
  const classes = useStyles();
  const innerContent = component ? component : <div>{result.item.value}</div>;
  return (
    <ListItem
      key={`${label}${result.item.id}`}
      onClick={onClick}
      className={classes.searchItem}>
      <Avatar
        className={classes.searchItemAvatar}
        style={{ backgroundColor: CATEGORY_COLORS[result.category] }}>
        {label.charAt(0).toUpperCase()}
      </Avatar>
      {innerContent}
    </ListItem>
  );
};

const Search: React.FC<SearchProps> = (props: SearchProps) => {
  const classes = useStyles();
  const search = useSelector((st: AppState) => st.search);
  const [navigateTo] = useNavigation();
  const renderItem = (
    item: SearchResult,
    index: number
  ): React.ReactElement | null => {
    if (isResultOfType(item, 'kural') && search.userSelections.kural) {
      return (
        <SearchResultItem
          key={index}
          result={item}
          label='k'
          // TODO:
          onClick={navigateTo(`/list/kural/${item.item.id}`)}
          component={
            <KuralCard
              componentType='span'
              key={item.item.id}
              kural={item.item}
              className={classes.kuralSearchItem}
            />
          }
        />
      );
    }
    if (isResultOfType(item, 'chapter') && search.userSelections.chapter) {
      return (
        <SearchResultItem
          key={index}
          label='ch'
          onClick={navigateTo(`/list/${item.item.id}/kural`)}
          result={item}
        />
      );
    }
    if (
      isResultOfType(item, 'literature') &&
      search.userSelections.literature
    ) {
      return (
        <SearchResultItem
          key={index}
          label='lit'
          onClick={navigateTo(`/list/${item.item.id}/chapter`)}
          result={item}
        />
      );
    }
    if (isResultOfType(item, 'section') && search.userSelections.section) {
      return (
        <SearchResultItem
          key={index}
          label='sec'
          onClick={navigateTo(`/list/${item.item.id}/literature`)}
          result={item}
        />
      );
    }
    // if (isResultOfType(item, 'detail') && search.userSelections.detail) {
    //   return <>detail: {item.item.value}</>;
    // }
    return null;
  };
  return (
    <div className={clsx(props.className, classes.root)}>
      <SearchChips />
      <List>{search.results.map((item, i) => renderItem(item, i))}</List>
    </div>
  );
};

export default Search;
