import {
  SearchResult,
  SearchTypes,
  Category,
  SearchCategoryTypes,
} from 'src/lib/api';

// ACTION TYPES
type SetSearchResults = 'SetSearchResults';
type AddSearchResults = 'AddSearchResults';
type SetSearchFilters = 'SetSearchFilters';
type SetUserSelection = 'SetUserSelection';
type SetAllUserSelection = 'SetAllUserSelection';
type SetSearchKeyword = 'SetSearchKeyword';
type SetSearchStatus = 'SetSearchStatus';

type SetSearchResultAction = {
  type: SetSearchResults;
  payload: SearchResult[];
};
type AddSearchResultsAction = {
  type: AddSearchResults;
  payload: SearchResult[];
};
type SetSearchFilterAction = {
  type: SetSearchFilters;
  payload: SearchTypes[];
};
type SetUserSelectionAction = {
  type: SetUserSelection;
  payload: [UserSelectionTypes, boolean];
};
type SetAllUserSelectionAction = {
  type: SetAllUserSelection;
  payload: boolean;
};
type SetSearchStatusAction = {
  type: SetSearchStatus;
  payload: boolean;
};

type SetSearchKeywordAction = {
  type: SetSearchKeyword;
  payload: string;
};

type SearchStateAction =
  | SetSearchKeywordAction
  | SetSearchStatusAction
  | SetSearchResultAction
  | SetSearchFilterAction
  | AddSearchResultsAction
  | SetAllUserSelectionAction
  | SetUserSelectionAction;

type UserSelectionTypes = Exclude<SearchCategoryTypes, 'all'>;

// Actions

export const setSearchStatus = (status: boolean): SetSearchStatusAction => ({
  type: 'SetSearchStatus',
  payload: status,
});
export const clearSearchResults = (): SetSearchResultAction => {
  return { type: 'SetSearchResults', payload: [] };
};
export const appendSearchResults = (
  results: SearchResult[]
): AddSearchResultsAction => {
  return { type: 'AddSearchResults', payload: results };
};

export const setUserSelection = (
  category: UserSelectionTypes,
  selection: boolean
): SetUserSelectionAction => {
  return { type: 'SetUserSelection', payload: [category, selection] };
};

export const setAllUserSelections = (
  state: boolean
): SetAllUserSelectionAction => {
  return { type: 'SetAllUserSelection', payload: state };
};

export const setSearchFilters = (
  results: SearchTypes[]
): SetSearchFilterAction => {
  return { type: 'SetSearchFilters', payload: results };
};

export const setSearchKeyword = (keyword: string): SetSearchKeywordAction => {
  return { type: 'SetSearchKeyword', payload: keyword };
};

const sortSearchResults = (items: SearchResult[]) => {
  const SortValueMap: Record<Category, number> = {
    kural: 1,
    chapter: 2,
    literature: 3,
    section: 4,
  };
  return items.sort(
    (a, b) => SortValueMap[a.category] - SortValueMap[b.category]
  );
};

// reducer
type SearchState = {
  /** show search bar */
  status: boolean;
  keyword: string;
  results: SearchResult[];
  filters: SearchTypes[];
  userSelections: Record<UserSelectionTypes, boolean>;
};
const defaultState: SearchState = {
  status: false,
  keyword: '',
  results: [],
  filters: [],
  userSelections: {
    chapter: true,
    kural: true,
    section: true,
    literature: true,
    detail: true,
  },
};
export const SearchStateReducer = (
  state = defaultState,
  action: SearchStateAction
): SearchState => {
  if (action.type === 'SetSearchKeyword' && action.payload !== state.keyword) {
    return { ...state, keyword: action.payload, results: [] };
  } else if (action.type === 'AddSearchResults') {
    return {
      ...state,
      results: sortSearchResults([...state.results, ...action.payload]),
    };
  } else if (action.type === 'SetSearchResults') {
    return {
      ...state,
      results: sortSearchResults(action.payload as SearchResult[]),
    };
  } else if (action.type === 'SetSearchFilters') {
    return { ...state, filters: action.payload as SearchTypes[] };
  } else if (action.type === 'SetUserSelection') {
    const [category, status] = action.payload as [Category, boolean];
    return {
      ...state,
      userSelections: {
        ...state.userSelections,
        [category]: status,
      },
    };
  } else if (action.type === 'SetAllUserSelection') {
    const status = action.payload as boolean;
    return {
      ...state,
      userSelections: {
        chapter: status,
        kural: status,
        section: status,
        literature: status,
        detail: status,
      },
    };
  } else if (action.type === 'SetSearchStatus') {
    return { ...state, status: action.payload };
  }
  return state;
};
