/* eslint-disable @typescript-eslint/no-non-null-assertion */
import React from 'react';

import log from 'loglevel';
import { useDispatch, useSelector } from 'react-redux';
import { useFirestore } from 'react-redux-firebase';
import { matchPath, useHistory } from 'react-router-dom';
import {
  CategoryBaseTypeMap,
  Category,
  getCategoryIds,
  RootCategory,
  findChapterNoFromKuralNo,
  IndexMapType,
  User,
  CATEGORY_COLORS,
} from 'src/lib/api';
import { ROUTE_CONSTANTS } from 'src/routes';
import { AppState } from 'src/state';
import { updateUserFSAsync } from 'src/state/settings';

export type RouteProp = {
  id: number;
  value: string;
  type: Category | 'top';
  isFavourite?: boolean;
  route?: string;
  color?: string;
};
type TopRouteMap = Partial<Record<RootCategory, number>>;
type RouteMap = Partial<Record<Category, number>>;

const getRoutes: (
  categories: Partial<CategoryBaseTypeMap>,
  favourites: Record<'kural' | 'chapter', number[]>
) => RouteProp[] = (categories, favourites) => {
  // TODO: move static content to firestore
  const routes: RouteProp[] = [
    {
      id: 0,
      value: '',
      type: 'top',
      route: ROUTE_CONSTANTS.LIST_CATEGORY,
      color: CATEGORY_COLORS.default,
    },
  ];
  if (categories.section) {
    routes.push({
      ...categories.section,
      type: 'section',
      route: `${ROUTE_CONSTANTS.LIST_CATEGORY}/${categories.section.id}`,
      color: CATEGORY_COLORS.section,
    });
  }
  if (categories.literature) {
    routes.push({
      ...categories.literature,
      type: 'literature',
      route: `${ROUTE_CONSTANTS.LIST_CATEGORY}/${categories.section!!.id}/${
        categories.literature.id
      }`,
      color: CATEGORY_COLORS.literature,
    });
  }
  if (categories.chapter) {
    routes.push({
      ...categories.chapter,
      isFavourite: favourites.chapter.some(
        (ch) => ch === categories.chapter?.id
      ),
      type: 'chapter',
      route: `${ROUTE_CONSTANTS.LIST}/${categories.section!!.id}/${
        categories.literature!!.id
      }/${categories.chapter.id}`,
      color: CATEGORY_COLORS.chapter,
    });
  }
  if (categories.kural) {
    routes.push({
      ...categories.kural,
      isFavourite: favourites.kural.some((k) => k === categories.kural?.id),
      id: categories.kural.id,
      value: `: ${categories.kural.id}`,
      type: 'kural',
      route: `${ROUTE_CONSTANTS.LIST}/${categories.section!!.id}/${
        categories.literature!!.id
      }/${categories.chapter!!.id}/${categories.kural.id}`,
      color: CATEGORY_COLORS.kural,
    });
  }
  return routes;
};

const rootCategoryFinder = (
  INDEX_MAP: IndexMapType,
  options: TopRouteMap
): TopRouteMap => {
  const category = Object.keys(options).find(
    (k: string) => options[k as RootCategory]
  ) as RootCategory;
  return INDEX_MAP && category
    ? // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
      getCategoryIds(INDEX_MAP, category, options[category]!!)
    : {};
};

type GetRouteValueType = () => Partial<CategoryBaseTypeMap>;
type useRouteType = (
  filter: RouteMap
) => [RouteMap, RouteProp[], (route: RouteProp) => void];
export const useRoute: useRouteType = (filter) => {
  const { section, literature, chapter, index: INDEX_MAP } = useSelector(
    (st: AppState) => st.kuralIndexMap
  );
  const user = useSelector((st: AppState) => st.preferences);
  const [inputFilter, setInputFilter] = React.useState(filter);
  const history = useHistory();
  const firestore = useFirestore();
  const dispatch = useDispatch();
  const [routeMap, setRouteMap] = React.useState<RouteMap>({});

  React.useEffect(() => {
    findFilters(history.location.pathname);
  }, [history.location.pathname]);

  React.useEffect(() => {
    if (INDEX_MAP) {
      const {
        kural: KURAL_NO,
        chapter: CHAPTER_NO,
        literature: LITERATURE_NO,
        section: SECTION_NO,
      } = inputFilter;
      const KURALS_CHAPTER = KURAL_NO
        ? findChapterNoFromKuralNo(KURAL_NO)
        : CHAPTER_NO;
      const routes = {
        ...rootCategoryFinder(INDEX_MAP, {
          chapter: KURALS_CHAPTER,
          literature: LITERATURE_NO,
          section: SECTION_NO,
        }),
        ...(KURAL_NO ? { kural: KURAL_NO } : {}),
      };
      log.debug('found routes:', routes);
      setRouteMap(routes);
    }
  }, [INDEX_MAP, inputFilter]);

  const findFilters = (pathname: string): void => {
    const sectionRoute = matchPath<{ section: string }>(pathname, {
      path: `${ROUTE_CONSTANTS.LIST_CATEGORY}/:section`,
      exact: true,
    });
    const literatureRoute = matchPath<{ literature: string }>(pathname, {
      path: `${ROUTE_CONSTANTS.LIST_CATEGORY}/:section/:literature`,
      exact: true,
    });
    // TODO: category & list route for chapter
    const chapterRoute = matchPath<{ chapter: string }>(pathname, {
      path: `${ROUTE_CONSTANTS.LIST}/:section/:literature/:chapter`,
      exact: true,
    });
    const kuralRoute = matchPath<{ kural: string }>(pathname, {
      path: `${ROUTE_CONSTANTS.LIST}/:section/:literature/:chapter/:kural`,
      exact: true,
    });
    const section = sectionRoute
      ? Number(sectionRoute?.params.section)
      : undefined;
    const literature = literatureRoute
      ? Number(literatureRoute?.params.literature)
      : undefined;
    const chapter = chapterRoute
      ? Number(chapterRoute?.params.chapter)
      : undefined;
    const kural = kuralRoute ? Number(kuralRoute?.params.kural) : undefined;
    const newFilter = {
      section,
      literature,
      chapter,
      kural,
    };
    // log.debug('route path values => ', JSON.stringify(newFilter));
    setInputFilter(newFilter);
  };

  history.listen(({ pathname }) => {
    // TODO: enable logs after debounceing
    // log.debug('detect route => ', pathname);
    findFilters(pathname);
  });

  const getRouteValue: GetRouteValueType = React.useCallback(() => {
    return Object.keys(routeMap).reduce((oldVal, k) => {
      const input = k as Category;
      let output;
      switch (input) {
        case 'section':
          output = section[routeMap[input]!!];
          break;
        case 'literature':
          output = literature[routeMap[input]!!];
          break;
        case 'chapter':
          output = chapter[routeMap[input]!!];
          break;
        case 'kural':
          // NOTE: setting kural value to empty string as this value not needed
          output = { id: routeMap[input]!!, value: '' };
          break;
      }
      return { ...oldVal, [input]: output };
    }, {});
  }, [chapter, literature, routeMap, section]);

  const toggleRouteFavourite = (route: RouteProp) => {
    if (route.type === 'kural' || route.type === 'chapter') {
      const shouldAddToList = !route.isFavourite;
      let favorites: number[];
      if (shouldAddToList) {
        favorites = [...user.favorites[route.type], route.id];
      } else {
        const itemIndex = user.favorites[route.type].indexOf(route.id);
        favorites = user.favorites[route.type];
        favorites.splice(itemIndex, 1);
      }
      const updatedUserPref: User = {
        ...user,
        favorites: {
          ...user.favorites,
          [route.type]: favorites,
        },
      };
      dispatch(updateUserFSAsync(firestore, updatedUserPref));
    }
  };
  // TODO: direct access of http://localhost:3000/#/list/kural/631 doesn't show routes
  return [
    routeMap,
    getRoutes(getRouteValue(), user.favorites),
    toggleRouteFavourite,
  ];
};
