import React from 'react';

import {
  makeStyles,
  List,
  ListItem,
  ListItemText,
  Select,
  FormControl,
  MenuItem,
} from '@material-ui/core';
import { useSelector, useDispatch } from 'react-redux';

import { AppState } from '../../../state/reducers';
import { applyPreference, DEFAULT } from '../../../state/settings';

const useStyles = makeStyles((theme) => ({
  root: {},
  item: {},
  itemText: {
    fontSize: '1em',
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
}));

const SettingsLabel = {
  FONT_SIZE: 'Font size',
};

type SettingsProps = {
  className?: string;
};

const getFontSizeOptions = (
  baseSize: number,
  count: number,
  offset = 2
): number[] => {
  const result = [];
  let size = baseSize;
  while (count > 0) {
    result.push(size);
    size = size + offset;
    count--;
  }
  return result;
};

const Settings: React.FC<SettingsProps> = (props: SettingsProps) => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const { preference } = useSelector((st: AppState) => st.preferences);
  const fontSizeOptions: Array<number> = getFontSizeOptions(
    DEFAULT.preference.fontSize - 4,
    10
  );
  const onFontChange = (fontSize: number): void => {
    dispatch(applyPreference(Object.assign(preference, { fontSize })));
  };

  return (
    <div className={props.className}>
      <List>
        <ListItem className={classes.item}>
          <ListItemText
            classes={{ primary: classes.itemText }}
            primary={SettingsLabel.FONT_SIZE}
          />
          <FormControl className={classes.formControl}>
            <Select
              value={preference.fontSize}
              onChange={(e): void => onFontChange(e.target.value as number)}>
              {fontSizeOptions.map((v) => (
                <MenuItem key={v} value={v}>
                  {v}
                </MenuItem>
              ))}
            </Select>
          </FormControl>
        </ListItem>
      </List>
    </div>
  );
};

export default Settings;
