import { InitIndexMapAction, KuralIndexMap } from './types';

const defaultState: KuralIndexMap = {
  chapter: {},
  section: {},
  literature: {},
  transliterator: {},
  index: null,
};

export const KuralIndexReducer = (
  state: KuralIndexMap = defaultState,
  action: InitIndexMapAction
): KuralIndexMap => {
  if (action.type === 'init') {
    return action.payload;
  }
  return state;
};
