import firebase from 'firebase/app';
import { ExtendedFirestoreInstance } from 'react-redux-firebase';

import { COLLECTION_NAME_MAP } from '.';
import { firestoreGetCacheFirst } from './firestore';
import { User } from './types';

type UserPreferenceDocument = Omit<User, 'id'>;
export const getUserPreferences = async (
  firestore: ExtendedFirestoreInstance,
  userId: string
): Promise<User> => {
  const snapshot = await firestoreGetCacheFirst(
    firestore
      .collection(COLLECTION_NAME_MAP.USER)
      .where(firebase.firestore.FieldPath.documentId(), '==', userId)
      .limit(1)
  );
  if (snapshot.empty) {
    throw new Error('user not found');
  }
  const userDocRef = snapshot.docs[0];
  return {
    id: userDocRef.id,
    ...(userDocRef.data() as UserPreferenceDocument),
  };
};

export const updateUserPreference = async (
  firestore: ExtendedFirestoreInstance,
  user: User
): Promise<void> => {
  await firestore
    .collection(COLLECTION_NAME_MAP.USER)
    .doc(`${user.id}`)
    .set(user);
};
