/* eslint-disable @typescript-eslint/no-non-null-assertion */
import { useSelector } from 'react-redux';

import { AppState } from '../../state';
import { RootCategory, BaseType, getCategoryIds } from '../api';

type getItemType = (chapterNo: number) => BaseType | null;
type getCategoryIdsType = (
  key: number
) => Partial<Record<RootCategory, number>>;
export type useItemType = (
  category: RootCategory
) => [getItemType, getCategoryIdsType];

export const useBaseItem: useItemType = (category) => {
  const { [category]: items, index: INDEX_MAP } = useSelector(
    (st: AppState) => st.kuralIndexMap
  );
  const getItem: getItemType = (itemNo) => items[itemNo] || null;
  const getCategoryRoute: getCategoryIdsType = (categoryId) =>
    getCategoryIds(INDEX_MAP!!, category, categoryId);
  return [getItem, getCategoryRoute];
};
