/* eslint-disable @typescript-eslint/no-non-null-assertion */
import {
  RootCategory,
  IndexMapType,
  isCategoryValidErr,
  isItemInRange,
} from './';
import { Category } from './types';

const getLiteratureNoFromChapterNo = (
  INDEX_MAP: IndexMapType,
  chapterNo: number
): number => {
  isCategoryValidErr('chapter', chapterNo);
  return Number(
    Object.keys(INDEX_MAP.litChapMap).find((litId) =>
      isItemInRange(chapterNo, INDEX_MAP.litChapMap[Number(litId)])
    )
  );
};

const getSectionNoByLiteratureNo = (
  INDEX_MAP: IndexMapType,
  literatureNo: number
): number => {
  isCategoryValidErr('literature', literatureNo);
  return Number(
    Object.keys(INDEX_MAP.section)
      .map((secId: string) => ({
        section: secId,
        litIndex: INDEX_MAP.section[Number(secId)].litIndex,
      }))
      .find((map) => isItemInRange(literatureNo, map.litIndex))?.section
  );
};

export const getCategoryIds = (
  INDEX_MAP: IndexMapType,
  category: RootCategory,
  categoryId: number
): Partial<Record<RootCategory, number>> => {
  isCategoryValidErr(category, categoryId);
  let result: Partial<Record<RootCategory, number>> = {
    [category]: categoryId,
  };
  switch (category) {
    case 'chapter':
      result = {
        ...result,
        literature: getLiteratureNoFromChapterNo(INDEX_MAP, result.chapter!!),
      };
    case 'literature':
      result = {
        ...result,
        section: getSectionNoByLiteratureNo(INDEX_MAP, result.literature!!),
      };
    case 'section':
      break;
  }
  return result;
};

export const CATEGORY_COLORS: Record<Category | 'default', string> = {
  section: 'rgba(162, 43, 47,0.8)',
  literature: 'cadetblue',
  chapter: 'rgba(215, 22, 64, 0.8)',
  kural: '#3f51b5',
  default: 'blueviolet',
};
