/* eslint-disable @typescript-eslint/no-non-null-assertion */
import React from 'react';

import { useSelector } from 'react-redux';
import { useFirestore } from 'react-redux-firebase';

import { AppState } from '../../state';
import {
  BaseType,
  Category,
  fillRange,
  getKuralsOfChapter,
  isCategoryValidErr,
  RootCategory,
} from '../api';

type useSubCategoryType = (
  category?: RootCategory,
  id?: number,
  resultCategory?: Category
) => [BaseType[], (id: number) => BaseType[], (id: number) => BaseType[]];
export const useSubCategory: useSubCategoryType = (
  category,
  id,
  resultCategory
) => {
  if (id && category && resultCategory) {
    if (category === resultCategory) {
      throw new Error('Bad input');
    }
    isCategoryValidErr(category, id);
  }

  const firestore = useFirestore();
  const [result, setResult] = React.useState<BaseType[]>([]);
  const { primary: PRIMARY_LANG } = useSelector(
    (st: AppState) => st.preferences.preference.language
  );
  const KURAL_INDEX = useSelector((st: AppState) => st.kuralIndexMap);

  const getLiteraturesBySection = React.useCallback(
    (sectionId: number): BaseType[] => {
      isCategoryValidErr('section', sectionId);
      return fillRange(KURAL_INDEX.index!!.section[sectionId].litIndex).map(
        (k) => KURAL_INDEX.literature[k]
      );
    },
    [KURAL_INDEX.index, KURAL_INDEX.literature]
  );

  const getChaptersFromLiterature = React.useCallback(
    (litId: number): BaseType[] => {
      isCategoryValidErr('literature', litId);
      return fillRange(KURAL_INDEX.index!!.litChapMap[litId]).map(
        (c) => KURAL_INDEX.chapter[c]
      );
    },
    [KURAL_INDEX.chapter, KURAL_INDEX.index]
  );
  React.useEffect(() => {
    if (!result.length && id) {
      if (category === 'chapter' && resultCategory === 'kural') {
        (async (): Promise<void> => {
          setResult(await getKuralsOfChapter(firestore, id, PRIMARY_LANG));
        })();
      } else {
        const { index: INDEX_MAP } = KURAL_INDEX;
        if (INDEX_MAP) {
          if (category === 'literature' && resultCategory === 'chapter') {
            setResult(getChaptersFromLiterature(id));
          } else if (
            category === 'section' &&
            resultCategory === 'literature'
          ) {
            setResult(getLiteraturesBySection(id));
          } else {
            throw new Error('category not supported');
          }
        }
      }
    }
  }, [
    result.length,
    category,
    resultCategory,
    firestore,
    id,
    PRIMARY_LANG,
    KURAL_INDEX,
    getLiteraturesBySection,
    getChaptersFromLiterature,
  ]);

  return [result, getLiteraturesBySection, getChaptersFromLiterature];
};
