import React from 'react';

import { RouteConfig } from 'react-router-config';
import { Redirect } from 'react-router-dom';

import { CategoryViewer } from './views/components/kural/CategoryViewer';
import KuralDetailViewer from './views/components/kural/KuralDetailViewer';
import { KuralListSwipeable } from './views/components/kural/KuralsByChapterViewer';
import Home from './views/pages/home';
import { ListWithRouteViewer } from './views/pages/list';
import Main from './views/pages/main/main';
import Search from './views/pages/search/search';
import SettingsViewer from './views/pages/settings';

export const ROUTE_CONSTANTS = {
  ROOT: '/',
  LIST: '/list',
  LIST_CATEGORY: '/list',
  SEARCH: '/search',
  FAVOURITE: '/favorite',
  SETTINGS: '/settings',
};

export const routes: RouteConfig[] = [
  {
    path: ROUTE_CONSTANTS.ROOT,
    component: Main,
    routes: [
      {
        path: ROUTE_CONSTANTS.SEARCH,
        component: Search,
        exact: true,
      },
      {
        path: ROUTE_CONSTANTS.SETTINGS,
        component: SettingsViewer,
        exact: true,
      },
      {
        path: ROUTE_CONSTANTS.LIST,
        component: ListWithRouteViewer,
        routes: [
          {
            path: `${ROUTE_CONSTANTS.LIST}/:section/:literature/:chapter`,
            component: KuralListSwipeable,
            exact: true,
          },
          {
            path: `${ROUTE_CONSTANTS.LIST}/:section/:literature/:chapter/:kural`,
            component: KuralDetailViewer,
            exact: true,
          },
          {
            path: `${ROUTE_CONSTANTS.LIST_CATEGORY}*`,
            component: CategoryViewer,
          },
          /* {
            path: `${ROUTE_CONSTANTS.LIST}*`,
            // eslint-disable-next-line react/display-name
            component: (): React.ReactElement => (
              <Redirect to={`${ROUTE_CONSTANTS.LIST}/1/1/1`} />
            ),
            exact: true,
          }, */
        ],
      },
      {
        path: ROUTE_CONSTANTS.ROOT,
        exact: true,
        component: Home,
      },
      {
        path: '/*',
        // eslint-disable-next-line react/display-name
        component: (): React.ReactElement => <Redirect to='/' />,
      },
    ],
  },
];
