import React from 'react';

import { Chip, makeStyles } from '@material-ui/core';
import { useSelector, useDispatch } from 'react-redux';

import { setAllUserSelections, setUserSelection } from '../../../state/search';
import {
  CATEGORY_COLORS,
  SearchCategoryLabelMap,
  SearchCategoryTypes,
} from './../../../lib/api';
import { AppState } from './../../../state';

type SearchChipsProps = unknown;
type FiltersType = {
  label: string;
  type: SearchCategoryTypes;
  isShow: boolean;
};

const useStyles = makeStyles((theme) => ({
  searchFilter: {
    paddingTop: theme.spacing(1),
    paddingBottom: theme.spacing(1),
    textAlign: 'start',
    '& .search-chip': {
      height: '2em',
      margin: '0 1vw 4px 0',
      fontSize: '0.7em',
      minWidth: '15vw',
      '& svg': {
        width: '1rem',
        height: '1rem',
        [theme.breakpoints.up('sm')]: {
          width: '1.5rem',
          height: '1.5rem',
        },
      },
    },
  },
}));

export const SearchChips: React.FC<SearchChipsProps> = (
  props: SearchChipsProps
) => {
  const classes = useStyles();
  const [filters, setFilters] = React.useState<FiltersType[]>([]);
  const search = useSelector((st: AppState) => st.search);
  const dispatch = useDispatch();
  React.useEffect(() => {
    const hasStartsWith = (keyword: string): boolean =>
      search.filters.filter((f) => f.startsWith(keyword)).length > 0;
    const filterOptions: FiltersType[] = [
      {
        label: SearchCategoryLabelMap.all,
        type: 'all',
        isShow: new Set(search.results.map((r) => r.type)).size > 1,
      },
      {
        label: SearchCategoryLabelMap.section,
        type: 'section',
        isShow: hasStartsWith('BySection'),
      },
      {
        label: SearchCategoryLabelMap.literature,
        type: 'literature',
        isShow: hasStartsWith('ByLiterature'),
      },
      {
        label: SearchCategoryLabelMap.chapter,
        type: 'chapter',
        isShow: hasStartsWith('ByChapter'),
      },
      {
        label: SearchCategoryLabelMap.kural,
        type: 'kural',
        isShow: hasStartsWith('ByKural'),
      },
      {
        label: SearchCategoryLabelMap.detail,
        type: 'detail',
        isShow: hasStartsWith('ByKuralDetail'),
      },
    ];
    setFilters(filterOptions);
  }, [search]);
  const isSelected = (type: SearchCategoryTypes): boolean => {
    if (type === 'all') {
      return (
        search.userSelections['section'] &&
        search.userSelections['literature'] &&
        search.userSelections['chapter'] &&
        search.userSelections['kural'] &&
        search.userSelections['detail']
      );
    } else {
      return search.userSelections[type];
    }
  };

  const onClick = (type: SearchCategoryTypes): void => {
    if (type !== 'all') {
      dispatch(setUserSelection(type, !search.userSelections[type]));
    } else {
      dispatch(setAllUserSelections(!isSelected('all')));
    }
  };
  return (
    <div className={classes.searchFilter}>
      {filters
        .filter((f) => f.isShow)
        .map((f, i) => (
          <Chip
            key={i}
            size='small'
            className='search-chip'
            label={f.label}
            color={'primary'}
            style={{
              backgroundColor:
                CATEGORY_COLORS[
                  f.type === 'all' || f.type === 'detail' ? 'default' : f.type
                ],
            }}
            variant={isSelected(f.type) ? 'default' : 'outlined'}
            onClick={(e): void => onClick(f.type)}
          />
        ))}
    </div>
  );
};
