import React from 'react';

import { Fade } from '@material-ui/core';
import InputBase from '@material-ui/core/InputBase';
import {
  makeStyles,
  Theme,
  createStyles,
  fade,
} from '@material-ui/core/styles';
import CancelIcon from '@material-ui/icons/Cancel';
import SearchIcon from '@material-ui/icons/Search';
import clsx from 'clsx';
import { debug } from 'loglevel';
import { matchPath, useHistory } from 'react-router-dom';
import { useDebouncedCallback } from 'use-debounce';

import { useKuralSearch } from '../../../lib/hooks';

const SEARCH_ROUTE = '/search';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: 'flex',
      alignItems: 'center',
      maxWidth: 400,
      border: '1px solid',
      borderRadius: '25px',
      borderColor: fade(theme.palette.grey[500], 0.5),
      padding: '0.2em 0.5em',
      color: theme.palette.primary.main,
      height: '1.6rem',
      '&.focus': {
        borderColor: theme.palette.primary.main,
        '& .search-icon, & .searchbar': {
          color: theme.palette.primary.main,
        },
      },
    },
    searchicon: {
      color: fade(theme.palette.grey[500], 0.5),
      cursor: 'pointer',
    },
    clearIcon: {
      cursor: 'pointer',
    },
    input: {
      flex: 1,
      padding: '0px 6px',
      width: '100%',
      color: theme.palette.primary.light,
    },
  })
);

type KuralSearchProps = {
  className?: string;
  onSearchTap?: () => void;
};

const KuralSearch: React.FC<KuralSearchProps> = (props: KuralSearchProps) => {
  debug('KuralSearch');
  const classes = useStyles();
  const history = useHistory();
  const inputRef = React.useRef<HTMLDivElement>(null);
  const [setSearchKey] = useKuralSearch();
  const debounced = useDebouncedCallback((value: string) => {
    setSearchKey(value);
  }, 500);
  const [keyword, setKeyword] = React.useState('');
  const isKeywordEmpty = (): boolean => keyword.trim().length === 0;
  const isSearchRoute = (): boolean =>
    matchPath(history.location.pathname, { path: SEARCH_ROUTE })?.isExact ||
    false;
  const [focus, setFocus] = React.useState(isSearchRoute());

  React.useEffect(() => {
    debounced.callback(keyword);
  }, [debounced, keyword]);

  history.listen(({ pathname }) => {
    if (pathname !== SEARCH_ROUTE) {
      setKeyword('');
      setFocus(false);
    } else {
      setFocus(true);
    }
  });
  const onSearchBarFocus = (): void => {
    if (history.location.pathname !== SEARCH_ROUTE) {
      debug('route -> search');
      history.push(SEARCH_ROUTE);
    }
  };
  return (
    <div className={clsx(classes.root, props.className, focus && 'focus')}>
      <SearchIcon
        onClick={() => {
          inputRef.current?.querySelector('input')?.focus();
        }}
        className={clsx(classes.searchicon, 'search-icon')}
      />
      <InputBase
        ref={inputRef}
        className={`searchbar ${classes.input}`}
        placeholder='Search 43, agara'
        inputProps={{ 'aria-label': 'Search' }}
        onChange={(e): void => {
          const text = e.target.value;
          setKeyword(text);
        }}
        value={keyword}
        onFocus={(): void => {
          onSearchBarFocus();
          props.onSearchTap && props.onSearchTap();
        }}
      />
      <Fade in={!isKeywordEmpty()}>
        <CancelIcon
          className={classes.clearIcon}
          onClick={(): void => {
            setKeyword('');
          }}
        />
      </Fade>
    </div>
  );
};

export default KuralSearch;
